<template>
  <div id="data-view">
    <div class="block-header"></div>
    <div class="block-content">
      <left-container
        :cameraInfo="camera"
        :regionStatistics="regionStatistics"
        :degree="degree"
        :cabinet="cabinet"
        :cookInfo="cookInfo"
      />
      <center-container
        :totalJoinOrg="totalJoinOrg"
        :realTimeOrgJoinInfo="realTimeOrgJoinInfo"
        :regionStatistics="regionStatistics"
      />
      <right-container
        :cameraTypeInfo="cameraTypeInfo"
        :cameraInfo="camera"
        :realTimeOrgJoinInfo="realTimeOrgJoinInfo"
        :companyRatio="companyRatio"
      />
    </div>
  </div>
</template>

<script>
import leftContainer from '../custom/leftContainer'
import rightContainer from '../custom/rightContainer'
import centerContainer from '../custom/centerContainer'

import {
  getRealTimeTotald,
  getCookWarn,
  getDegree,
  getCabinet,
  getTotalOrgStatistics,
  getRegionStatistics,
  getRealTimeOrgJoin,
  getCategoryJoinTotal,
  getCompanyRatio
} from '../../service/service'
const success = '0000'
export default {
  name: 'DataView',
  components: {
    leftContainer,
    centerContainer,
    rightContainer
  },
  data () {
    return {
      camera: {
        offline: 0,
        online: 0,
        onlineRatio: '100',
        total: 0
      },
      regionStatistics: [], // 地区接入数量
      realTimeOrgJoinInfo: [], // 实时接入信息
      totalJoinOrg: {
        // 总接入数量
        all: 0,
        todayTotal: 0,
        previousTotal: 0
      },
      cameraTypeInfo: {
        categoryTotal: [],
        lastWeekCameraTotal: 0,
        lastWeekOrgTotal: 0
      },
      degree: {
        humidityMap: {
          twentyFour: 4,
          four: 0,
          sixteen: 0,
          twelve: 0,
          eight: 0,
          twenty: 0
        },
        temperatureMap: {
          twentyFour: 0,
          four: 0,
          sixteen: 0,
          twelve: 0,
          eight: 0,
          twenty: 0
        },
        certificateMap: {
          twentyFour: 0,
          four: 0,
          sixteen: 0,
          twelve: 0,
          eight: 0,
          twenty: 0
        }
      },
      cabinet: {}, // 消毒柜、留样柜、紫外灯
      cookInfo: {}, // 消毒柜、留样柜、紫外灯
      companyRatio: {
        companyRatio: {},
        companyTotal: {}
      }
    }
  },
  mounted () {
    this.timer = setInterval(this.init, 120 * 1000)
  },
  created () {
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    init () {
      this.getRealTimeTotald()
      this.getCookWarn()
      this.getDegree()
      this.getCabinet()
      this.getTotalOrgStatistics()
      this.getRegionStatistics()
      this.realTimeOrgJoinFun()
      this.getCategoryJoinTotal()
      this.getCompanyRatioFun()
    },
    // 实时摄像头统计
    getRealTimeTotald () {
      getRealTimeTotald().then(res => {
        const { code, data } = res
        if (code === success) {
          this.camera = {
            offline: data.offline || 0,
            online: data.online || 0,
            onlineRatio: data.onlineRatio || '100',
            total: data.total || 0
          }
        }
      })
    },
    getCookWarn () {
      getCookWarn().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cookInfo = data
        }
      })
    },
    getDegree () {
      getDegree().then(res => {
        const { code, data } = res
        if (code === success) {
          this.degree = data
        }
      })
    },
    getCabinet () {
      getCabinet().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cabinet = data
        }
      })
    },
    // 共接入单位统计
    getTotalOrgStatistics () {
      getTotalOrgStatistics().then(res => {
        const { code, data } = res
        if (code === success) {
          let all = (parseInt(data.todayTotal || 0) + data.previousTotal || 0)
          this.totalJoinOrg = {
            all: all,
            todayTotal: data.todayTotal || 0,
            previousTotal: data.previousTotal || 0
          }
        }
      })
    },
    // 各地市接入单位数量统计排行
    getRegionStatistics () {
      getRegionStatistics().then(res => {
        const { code, data } = res
        if (code === success) {
          if (data && data.length) {
            const maxLen = data[0].count
            data.map(item => {
              if (item.count) {
                item.width = (item.count / maxLen * 100) + '%'
              } else {
                item.width = '10px'
              }
            })
          }
          this.regionStatistics = data
        }
      })
    },
    // 实时单位接入统计（按单位的create_time来计算）
    realTimeOrgJoinFun () {
      getRealTimeOrgJoin().then(res => {
        const { code, data } = res
        if (code === success) {
          this.realTimeOrgJoinInfo = data && data.length ? data : []
        }
      })
    },
    // 摄像头分类接入统计
    getCategoryJoinTotal () {
      getCategoryJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cameraTypeInfo = {
            categoryTotal: data.categoryTotal || [],
            lastWeekCameraTotal: data.lastWeekCameraTotal | 0,
            lastWeekOrgTotal: data.lastWeekOrgTotal | 0
          }
        }
      })
    },
    getCompanyRatioFun () {
      getCompanyRatio().then(res => {
        const { code, data } = res
        if (code === success) {
          this.companyRatio = data
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
#data-view {
  zoom: 1;
  width: 7680px;
  height: 2160px;
  margin: 0;
  padding: 0;
  color: #fff;
  background: url("../../assets/img/bg2.jpg") no-repeat center;
  background-size:100%;
  display: flex;
  flex-direction: column;
  .block-content {
    width: 100%;
    height: 1780px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 98px;
  }
  .block-header {
    width: 100%;
    display: -webkit-box;
    -webkit-box-flex: 1;
    flex: 1;
  }
}
</style>
