<template>
  <block title="">
      <div class="chart-box" ref="chartRef">

      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const options = {
  tooltip: {
    trigger: 'axis'
  },
  grid: {
    top: '100', // 距上边距
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  calculable: false,
  legend: {
    data: ['消毒柜', '留样柜', '紫外灯'],
    itemGap: 60,
    textStyle: {
      color: '#e9ebee',
      fontSize: 20
    },
    right: '5%',
    padding: [30, 10]
  },
  xAxis: [
    {
      type: 'category',
      boundaryGap: true,
      data: [ '04', '08', '12', '16', '20', '24'],
      splitLine: {
        show: false
      },
      axisLabel: {
        show: true,
        textStyle: {
          color: '#fff',
          align: 'center'
        }
      }

    }
  ],
  yAxis: [
    {
      type: 'value',

      axisLabel: {
        formatter: '{value} ',
        textStyle: {
          color: '#fff',
          align: 'right'
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: 'dashed',
          color: '#00e4ff',
          opacity: 0.2
        }
      }
    }

  ],
  series: [
    {
      name: '消毒柜',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#18e4c5'
        }
      },
      barMinHeight: 2
    },
    {
      name: '留样柜',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#3e93f5'
        }
      },
      barMinHeight: 2
    },
    {
      name: '紫外灯',
      type: 'line',
      data: [],
      itemStyle: {
        normal: {
          color: '#ffb017'
        }
      }
    }
  ]
}

export default {
  name: 'sterilizer',
  props: {
    cabinet: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(options)
    },
    setChartData () {
      const { sampleCabinetMap, disinfectionCabinetMap, lampMap } = this.cabinet
      let newSeries = []
      if (disinfectionCabinetMap) {
        newSeries.push({
          name: '消毒柜',
          type: 'bar',
          data: disinfectionCabinetMap,
          itemStyle: {
            normal: {
              color: '#18e4c5'
            }
          }
        })
      }
      if (sampleCabinetMap) {
        newSeries.push({
          name: '留样柜',
          type: 'bar',
          data: sampleCabinetMap,
          itemStyle: {
            normal: {
              color: '#3e93f5'
            }
          }
        })
      }
      if (lampMap) {
        newSeries.push({
          name: '紫外灯',
          type: 'line',
          data: lampMap,
          itemStyle: {
            normal: {
              color: '#ffb017'
            }
          }
        })
      }
      options.series = newSeries
      this.chart.setOption(options)
    }
  },
  watch: {
    cabinet: {
      handler (newValue, oldValue) {
        console.log('cabinet data changed....')
        this.setChartData()
      },
      deep: true
    }
  }

}
</script>

<style lang="less" scoped>
.chart-box{
  width: 100%;
  height:100%
}
</style>
