<template>
  <div class="main">
    <div class="content">
      <div class="title">
        <div class="text">摄像在线率</div>
        <div class="img">
          <div class="item"></div>
          <div class="item"></div>
          <div class="item"></div>
          <div class="item"></div>
          <div class="item"></div>
          <div class="item"></div>
          <div class="item"></div>
          <div class="item"></div>
        </div>
      </div>
      <div class="line"></div>
      <div class="detail">
        <div class="chart-box" ref="chartRef"></div>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'

const option = {
  series: {
    type: 'pie',
    clockWise: false,
    radius: [97, 100],
    hoverAnimation: false,
    silent: true,
    center: ['50%', '50%'],
    data: [{
      value: 75,
      name: 'invisible',
      itemStyle: {
        normal: {
          color: '#7bf5fe',
          borderWidth: 1,
          borderColor: '#073A66'

        }
      },
      label: {
        show: false
      },
      labelLine: {
        show: false
      }
    }, {
      value: 25,
      label: {
        normal: {
          rich: {
            a: {
              color: '#7bf5fe',
              align: 'center',
              fontSize: 64,
              fontWeight: 'bold',
              fontFamily: 'SourceHanSansCN-Medium'
            },
            b: {
              color: '#7bf5fe',
              align: 'center',
              fontSize: 34
            },
            c: {
              fontSize: 34,
              fontFamily: 'SourceHanSansCN-Medium',
              fontWeight: 'bold'
            }
          },
          formatter: function (params) {
            return '{a|' + params.value + '}' + ' {c|%}' + '\n\n{b|在线}'
          },
          position: 'center',
          show: true,
          textStyle: {
            fontSize: '14',
            fontWeight: 'normal',
            color: '#fff'
          }
        }
      },

      itemStyle: {
        normal: {
          label: {
            show: false
          },
          labelLine: {
            show: false
          },
          color: '#7bf5fe',
          shadowColor: '#7bf5fe',
          borderWidth: 12,
          borderColor: '#82ffff',
          shadowBlur: 5
        }
      }
    }]
  }
}
export default {
  name: 'OnlineProbability',
  props: {
    onlineRatio: String
  },
  data () {
    return {
      option: {}
    }
  },
  computed: {
    getOnlineRatio () {
      return this.onlineRatio
    }
  },
  mounted () {
    this.initChart()
  },

  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option)
    }
  },
  watch: {
    getOnlineRatio (newV, oldV) {
      const v = parseInt(newV)
      option.series.data[0].value = 100 - v
      option.series.data[1].value = v
      this.chart.setOption(option)
    }
  }
}
</script>

<style lang="less" scoped>
.main {
  margin: 15px;
  .content {
    width: 686px;
    height: 356px;
    margin: 53px 41px 49px 39px;
    .title {
      height: 30px;
      display: flex;
      flex-direction: row;
      align-content: flex-start;
      align-items: center;
      justify-content: flex-start;
      .text {
        text-align: left;
        flex: 1;
        height: 34px;
        font-family: SourceHanSansCN-Medium;
        font-size: 36px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 48px;
        letter-spacing: 0px;
        color: #00e4ff;
      }
      .img {
        width: 100px;
        display: flex;
        flex-direction: row;
        .item {
          display: block;
          height: 20px;
          width: 5px;
          margin-right: 8px;
          background-color: #00e4ff;
          transform: skewX(-40deg);
        }
      }
    }
    .line {
      display: block;
      width: 686px;
      height: 3px;
      margin-top: 30px;
      background-color: #00e4ff;
      opacity: 0.07;
    }
    .detail {
      width: 656px;
      height: 250px;
      display: flex;
      align-content: center;
      justify-content: center;
      align-items: center;
      text-align: center;
      .chart-box {
        margin-top: 50px;
      }
    }
  }
}
</style>
