<template>
  <div class="main"  ref="chartRef">
  </div>
</template>

<script>
import echarts from 'echarts'

const option = {
  series: {
    type: 'pie',
    clockWise: false,
    radius: [87, 90],
    hoverAnimation: false,
    silent: true,
    center: ['50%', '50%'],
    data: [{
      value: 75,
      name: 'invisible',
      itemStyle: {
        normal: {
          color: '#7bf5fe',
          borderWidth: 1,
          borderColor: '#073A66'

        }
      },
      label: {
        show: false
      },
      labelLine: {
        show: false
      }
    }, {
      value: 0,
      label: {
        normal: {
          rich: {
            a: {
              color: '#7bf5fe',
              align: 'center',
              fontSize: 62,
              fontWeight: 'bold',
              fontFamily: 'SourceHanSansCN-Medium'
            },
            b: {
              color: '#7bf5fe',
              align: 'center',
              fontSize: 34
            },
            c: {
              fontSize: 34,
              fontFamily: 'SourceHanSansCN-Medium',
              fontWeight: 'bold'
            }
          },
          formatter: function (params) {
            return '{a|' + params.value + '}' + ' {c|%}' + '\n\n{b|在线}'
          },
          position: 'center',
          show: true,
          textStyle: {
            fontSize: '14',
            fontWeight: 'normal',
            color: '#fff'
          }
        }
      },

      itemStyle: {
        normal: {
          label: {
            show: false
          },
          labelLine: {
            show: false
          },
          color: '#7bf5fe',
          shadowColor: '#7bf5fe',
          borderWidth: 12,
          borderColor: '#82ffff',
          shadowBlur: 5
        }
      }
    }]
  }
}
export default {
  name: 'LeftTopChart',
  props: {
    onlineRatio: String
  },
  data () {
    return {
      option: {}
    }
  },
  computed: {
    getOnlineRatio () {
      return this.onlineRatio
    }
  },
  mounted () {
    this.initChart()
  },
  created () {
    console.log(this.onlineRatio)
  },

  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option)
    }
  },
  watch: {
    getOnlineRatio (newV, oldV) {
      const v = parseInt(newV)
      option.series.data[0].value = 100 - v
      option.series.data[1].value = v
      this.chart.setOption(option)
    }
  }
}
</script>

<style lang="less" scoped>
.main {
  width: 100%;
  height: 100%;
}
</style>
