<template>
  <block title="">
      <div class="chart-box" ref="chartRef">
      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const dataIPSxAxis = ['04', '08', '12', '16', '20', '24']
const dataIPS = []
const dataIPS2 = []
const dataIPS3 = []

const options = {

  tooltip: {
    trigger: 'axis',
    axisPointer: {
      type: 'cross',
      label: {
        backgroundColor: '#6a7985'
      }
    }
  },
  legend: {
    data: ['未戴白色厨帽', '未戴白色口罩', '未穿白色厨师服'],
    itemGap: 90,
    textStyle: {
      color: '#e9ebee',
      fontSize: 20
    },
    right: '5%',
    padding: [30, 10]

  },
  color: ['#0080ff', '#4cd5ce', '#bf4696'],
  toolbox: {
    // feature: {
    //     saveAsImage: {}
    // }
  },
  grid: {
    top: '100', // 距上边距
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  xAxis: [
    {
      type: 'category',
      boundaryGap: true,
      data: [ '04', '08', '12', '16', '20', '24'],
      splitLine: {
        show: false
      },
      axisLabel: {
        show: true,
        textStyle: {
          color: '#fff',
          align: 'center'
        }
      }

    }
  ],
  yAxis: [{
    type: 'value',
    axisLine: {
      onZero: false,
      lineStyle: {
        color: '#0a2b52',
        width: 1 // 这里是为了突出显示加上的
      },
      textStyle: {
        color: '#fff'
      }

    },

    axisLabel: {
      formatter: function (val) {
        return val + ''
      },
      show: true,
      textStyle: {
        color: '#fff'
      }
    },
    splitLine: {
      show: true,
      lineStyle: {
        type: 'dashed',
        color: '#00e4ff',
        opacity: 0.2
      }

    }
  }],
  series: [
    {
      name: '未戴白色厨帽',
      type: 'line',
      smooth: true,
      symbol: 'none', // 去掉折线点
      itemStyle: {
        normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: 'rgba(176,242,232,0.5)' // 0% 处的颜色
          }, {
            offset: 0.5,
            color: 'rgba(176,242,232,0.5)' // 100% 处的颜色
          }, {
            offset: 1,
            color: 'rgba(56,226,200,0.5)' // 100% 处的颜色
          }]), // 背景渐变色
          lineStyle: { // 系列级个性化折线样式
            width: 1,
            type: 'solid',
            color: '#18e4c5'
          }
        },
        emphasis: {
          color: '#18e4c5',
          lineStyle: { // 系列级个性化折线样式
            width: 1,
            type: 'dotted',
            color: '#18e4c5' // 折线的颜色
          }
        }
      }, // 线条样式
      symbolSize: 5, // 折线点的大小
      areaStyle: {
        normal: {}
      },
      data: dataIPS
    },

    {
      name: '未戴白色口罩',
      type: 'line',
      smooth: true,
      symbol: 'none', // 去掉折线点
      itemStyle: {
        normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: 'rgba( 100, 202, 250, 0.5)' // 0% 处的颜色
          }, {
            offset: 0.5,
            color: 'rgba( 100, 202, 250, 0.5)' // 100% 处的颜色
          }, {
            offset: 1,
            color: 'rgba(0, 120, 215, 0.5)' // 100% 处的颜色
          }]), // 背景渐变色
          lineStyle: { // 系列级个性化折线样式
            width: 1,
            type: 'solid',
            color: '#0078D7'
          }
        },
        emphasis: {
          color: '#02675f',
          lineStyle: { // 系列级个性化折线样式
            width: 1,
            type: 'dotted',
            color: '#02675f' // 折线的颜色
          }
        }
      }, // 线条样式
      symbolSize: 5, // 折线点的大小
      areaStyle: {
        normal: {}
      },
      data: dataIPS2
    },
    {
      name: '未穿白色厨师服',
      type: 'line',
      smooth: true,
      symbol: 'none', // 去掉折线点
      itemStyle: {
        normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: 'rgba(189,180,194,0.5)' // 0% 处的颜色
          }, {
            offset: 0.5,
            color: '#7b5e8a' // 100% 处的颜色
          }, {
            offset: 1,
            color: 'rgba(123,94,138,0.5)' // 100% 处的颜色
          }]), // 背景渐变色
          lineStyle: { // 系列级个性化折线样式
            width: 1,
            type: 'solid',
            color: 'rgba(244,142,186,0.5)'
          }
        },
        emphasis: {
          color: '#f48eba',
          lineStyle: { // 系列级个性化折线样式
            width: 1,
            type: 'dotted',
            color: '#f48eba' // 折线的颜色
          }
        }
      }, // 线条样式
      symbolSize: 5, // 折线点的大小
      areaStyle: {
        normal: {}
      },
      data: dataIPS3
    }

  ]
}
export default {
  name: 'cook',
  props: {
    cookInfo: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(options)
    },
    setChartData () {
      const { hatMap, maskMap, clothesMap } = this.cookInfo
      let newSeries = []
      options.series.map(item => {
        if (item.name === '未戴白色厨帽') {
          if (hatMap) {
            item.data = hatMap
          }
        } else if (item.name === '未穿白色厨师服') {
          if (clothesMap) {
            item.data = clothesMap
          }
        } else {
          if (maskMap) {
            item.data = maskMap
          }
        }
      })
      console.log('cook.....', options)
      this.chart.setOption(options)
    }
  },
  watch: {
    cookInfo: {
      handler (newValue, oldValue) {
        console.log('cookInfo data changed....')
        this.setChartData()
      },
      deep: true
    }
  }

}
</script>

<style lang="less">
.chart-box{
  width: 100%;
  height:100%
}
</style>
