<template>
  <div clas="main">
    <dv-scroll-board style="width: 1230px;height: 466px;margin-left:90px;margin-right:4px;margin-top:50px"  :config="config" />
  </div>
</template>

<script>
export default {
  name: 'RealTimeAccess',
  props: {
    realTimeOrgJoinInfo: Array
  },
  data () {
    return {
      config: {
        header: ['<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">餐饮单位名称</span>',
          '<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">安装数量</span>',
          '<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">在线数量</span>',
          '<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">接入时间</span>'],
        data: [],
        align: ['left'],
        rowNum: 5,
        columnWidth: [600, 150, 150],
        headerBGC: 'rgba(66,159,227,0.12)',
        headerHeight: 88,
        oddRowBGC: 'rgba(66,159,227,0.12)',
        evenRowBGC: 'rgba(66,159,227,0.12)'
      }
    }
  },
  computed: {
    realTimeOrgJoinInfoLen () {
      console.log(this.realTimeOrgJoinInfo.length)
      return this.realTimeOrgJoinInfo.length
    }
  },

  components: {
  },
  watch: {
    realTimeOrgJoinInfoLen (newV, oldV) {
      if (newV) {
        let data = []
        this.realTimeOrgJoinInfo.map(item => {
          data.push(
            [`<span style="font-size: 30px;line-height: 24px;height: 28px;color: #5bcde4;">${item.orgName || ''}</span>`,
              `<span style="font-size: 30px;line-height: 24px;height: 28px;color: #5bcde4;">${item.total || '0'}</span>`,
              `<span style="font-size: 30px;line-height: 24px;height: 28px;color: #5bcde4;">${item.onlineTotal || '0'}</span>`,
              `<span style="font-size: 30px;line-height: 24px;height: 28px;color: #5bcde4;">${item.createTime || ''}</span>`]
          )
        })
        this.config = {
          header: ['<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">餐饮单位名称</span>',
            '<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">安装数量</span>',
            '<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">在线数量</span>',
            '<span style="font-size: 28px;;line-height: 24px;color: #9bdef0;">接入时间</span>'],
          data: data,
          align: ['left'],
          rowNum: 5,
          columnWidth: [600, 150, 150],
          headerBGC: 'rgba(66,159,227,0.12)',
          headerHeight: 88,
          oddRowBGC: 'rgba(66,159,227,0.12)',
          evenRowBGC: 'rgba(66,159,227,0.12)'
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.main{
  box-sizing: border-box;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items:center;
  align-content: center;
  justify-items: center;
  justify-items: center;
  text-align: center;

}
</style>
