        line-height: 48px;
        letter-spacing: 5px;
        color: #f7e0b9;
        text-align: right;
      }
    }
    .detail {
      margin-top: 59px;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      .unit {
        width: 43px;
        height: 44px;
        font-family: SourceHanSansCN-Regular;
        font-size: 48px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 48px;
        letter-spacing: 1px;
        color: #f7e0b9;
        text-align: left;
        margin-top: 100px;
      }
      .data_show_box {
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        justify-content: flex-start;
