        height: 146px;
        .data_cage {
          width: 96px;
          height: 146px;
          background-image: linear-gradient(
            0deg,
            #9cecfb 0%,
            #189ba9 51%,
            #65c7f7 100%
          );
          border: solid 1px #65ffff;
          margin-left: 20px;
          text-align: center;
          font-family: SourceHanSansCN-Regular;
          font-size: 95px;
          font-weight: normal;
          font-stretch: normal;
          line-height: 146px;
          letter-spacing: 2px;
          color: #ffffff;
        }
      }
    }
    .yesterday {
      width: 100%;
      height: 50px;
