        line-height: 55px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0;
        margin: 55px 0 0 0;
        padding: 0;
        color: #43ebff;
        .item div {
          height: 50px;
        }
      }
    }
  }
  .bottom {
    width: 650px;
    position: absolute;
    left: 2718px;
    top: 1900px;
    z-index: 9999;
    .time {
      width: 100%;
      height: 71px;
      font-family: Helvetica;
      font-size: 98px;
