<script>
import block from '../../component/block'
import watch from '../custom/right/camera-type/watch'
import realTimeAccess from '../custom/right/unitType/realTimeAccess'
import unitType from '../custom/right/unitType/unitType'
import onLineChart from './right/camera-type/chart/chart'

import { getWeekIncrTotal } from '../../service/service'
import recordStatus from '../../component/status'
const success = '0000'
export default {
  name: 'RightContainer',
  props: {
    cameraTypeInfo: Object,
    cameraInfo: Object,
    realTimeOrgJoinInfo: Array,
    companyRatio: Object
  },
  data () {
    return {
      orgIncrTotal: {
        cameraIncrTotal: 0,
        orgIncrTotal: 0
      }
    }
  },
  computed: {
    shcy () {
      const categoryTotal = this.cameraTypeInfo.categoryTotal
      for (let i = 0; i < categoryTotal.length; i++) {
        if (categoryTotal[i]['社会餐饮']) {
          return categoryTotal[i]['社会餐饮']
        }
      }
      return {}
    },
    zgst () {
      const categoryTotal = this.cameraTypeInfo.categoryTotal
      for (let i = 0; i < categoryTotal.length; i++) {
        if (categoryTotal[i]['职工食堂']) {
          return categoryTotal[i]['职工食堂']
        }
      }
      return {}
    },
    xxst () {
      const categoryTotal = this.cameraTypeInfo.categoryTotal
      for (let i = 0; i < categoryTotal.length; i++) {
        if (categoryTotal[i]['学校食堂']) {
          return categoryTotal[i]['学校食堂']
        }
      }
      return {}
    },
    jtjc () {
      const categoryTotal = this.cameraTypeInfo.categoryTotal
      for (let i = 0; i < categoryTotal.length; i++) {
        if (categoryTotal[i]['集体聚餐']) {
          return categoryTotal[i]['集体聚餐']
        }
      }
      return {}
    },
    ygcj () {
      const categoryTotal = this.cameraTypeInfo.categoryTotal
      for (let i = 0; i < categoryTotal.length; i++) {
        if (categoryTotal[i]['阳光车间']) {
          return categoryTotal[i]['阳光车间']
        }
      }
      return {}
    },
    all () {
      const categoryTotal = this.cameraTypeInfo.categoryTotal
      for (let i = 0; i < categoryTotal.length; i++) {
        if (categoryTotal[i]['all']) {
          return categoryTotal[i]['all']
        }
      }
      return {}
    }
  },
  components: {
    block, watch, realTimeAccess, unitType, onLineChart, recordStatus
  },
  created () {
    this.getWeekIncrTotalFun()
  },
  methods: {
    // 近一周新增单位、摄像头
    getWeekIncrTotalFun () {
      getWeekIncrTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.orgIncrTotal = {
            cameraIncrTotal: data.cameraIncrTotal || 0,
            orgIncrTotal: data.orgIncrTotal || 0
          }
        }
      })
    }
  }
}
</script>

