<template>
  <span
      v-bind:class="[{'status-red':incrRatio&&(incrRatio!==0 && incrRatio!=='0'&&incrRatio.indexOf('-')===-1)},{'status-green':incrRatio&&(incrRatio!==0 && incrRatio!=='0'&&incrRatio.indexOf('-')===-1)}]"
    >{{incrRatio && incrRatio===0 ||incrRatio==='0'?'-':(incrRatio&&incrRatio.indexOf('-')!==-1?'↓':'↑')}}</span>
</template>
<script>
export default {
  name: 'recordStatus',
  props: {
    incrRatio: {
      type: String,
      default: '0'
    }
  }
}
</script>
<style scoped>
.status-red {
  font-family: SourceHanSansCN-Regular;
  font-size: 28px;
  font-weight: normal;
  letter-spacing: 0px;
  color: #ff1818;
}
.status-green {
  font-family: SourceHanSansCN-Regular;
  font-size: 28px;
  font-weight: normal;
  letter-spacing: 0px;
  color: #6ceb35;
}
</style>
