import {
  get
} from '../util/http'

// ---------------------------------------------------------------- 摄像头统计
const getCameraById = (id) => {
  return get(`/camera/get/${id}`)
}

// 实时摄像头统计
const getRealTimeTotald = () => {
  return get('/camera/realTimeTotal')
}
// 摄像头分类接入统计
const getCategoryJoinTotal = () => {
  return get('/camera/categoryJoinTotal')
}

// ---------------------------------------------------------------- 接入单位统计

const getTotalOrgStatistics = () => {
  return get('/org/total')
}

const getRealTimeOrgJoin = () => {
  return get('/org/realTimeOrgJoin')
}

const getRegionStatistics = () => {
  return get('/org/region')
}
const getCompanyRatio = () => {
  return get('/org/companyRatio')
}
const getWeekIncrTotal = () => {
  return get('/org/weekIncrTotal')
}

// ---------------------------------------------------------------- 告警信息
// 获取厨师穿戴衣帽告警信息
const getCookWarn = () => {
  return get('/warning/cook')
}

// 温度、湿度、持证上岗统计
const getDegree = () => {
  return get('/warning/degree')
}

// 消毒柜、留样柜、紫外灯
const getCabinet = () => {
  return get('/warning/cabinet')
}

// ---------------------------------------------------------------- 地图
const getHenanMap = (id) => {
  return get(`/mam/henan`)
}

export {
  getCameraById,
  getRealTimeTotald,
  getCategoryJoinTotal,
  getTotalOrgStatistics,
  getRealTimeOrgJoin,
  getRegionStatistics,
  getCookWarn,
  getDegree,
  getCabinet,
  getHenanMap,
  getCompanyRatio,
  getWeekIncrTotal
}
