<template>
  <block title="">
      <div class="chart-box" ref="chartRef">

      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const myColor = ['#eb2100', '#eb3600', '#d0570e', '#d0a00e', '#34da62', '#00e9db', '#00c0e9', '#0096f3']
const option = {
  backgroundColor: '#0e2147',
  grid: {
    left: '11%',
    top: '12%',
    right: '0%',
    bottom: '8%',
    containLabel: true
  },
  xAxis: [{
    show: false
  }],
  yAxis: [{
    axisTick: 'none',
    axisLine: 'none',
    offset: '27',
    axisLabel: {
      textStyle: {
        color: '#ffffff',
        fontSize: '16'
      }
    },
    data: ['8', '7', '6', '5', '4', '3', '2', '1']
  }, {
    axisTick: 'none',
    axisLine: 'none',
    axisLabel: {
      textStyle: {
        color: '#ffffff',
        fontSize: '16'
      }
    },
    data: ['12567', '12566', '12566', '12566', '12566', '12566', '12566', '12566']
  }, {
    name: '单位：件',
    nameGap: '50',
    nameTextStyle: {
      color: '#ffffff',
      fontSize: '16'
    },
    axisLine: {
      lineStyle: {
        color: 'rgba(0,0,0,0)'
      }
    },
    data: []
  }],
  series: [{
    name: '条',
    type: 'bar',
    yAxisIndex: 0,
    data: [52, 60, 22, 33, 44, 78, 59, 15],
    label: {
      normal: {
        show: true,
        position: ['1%', '-10'],
        formatter: function (param) {
          return param.value + '%'
        },
        textStyle: {
          color: '#ffffff',
          fontSize: '16'
        }
      }
    },
    barWidth: 12,
    itemStyle: {
      normal: {
        color: function (params) {
          var num = myColor.length
          return myColor[params.dataIndex % num]
        }
      }
    },
    z: 2
  }, {
    name: '白框',
    type: 'bar',
    yAxisIndex: 1,
    barGap: '-100%',
    data: [99, 99.5, 99.5, 99.5, 99.5, 99.5, 99.5, 99.5],
    barWidth: 20,
    itemStyle: {
      normal: {
        color: '#0e2147',
        barBorderRadius: 5
      }
    },
    z: 1
  }]
}
export default {
  name: 'AccessUnit',

  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option)
    }
  }

}
</script>

<style lang="less" scoped>

</style>
