<template>
  <div class="main">
    <div class="chart-box" ref="chartRef"></div>
    <div class="online-number">
      实时在线：
      <span class="unit">{{cameraInfo.online}}</span>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'

const
  option = {
    title: {
      textStyle: {
        color: '#fff',
        fontSize: 70,
        fontWeight: 'bold'
      },
      left: 'center',
      bottom: '49%'

    },
    tooltip: {
      show: false
    },

    series: [
      {
        name: '数据',
        type: 'pie',
        startAngle: 225,
        radius: [142, '80%'],
        legendHoverLink: false,
        hoverAnimation: false,
        avoidLabelOverlap: false,
        labelLine: {
          normal: {
            show: false
          }
        },
        label: {
          normal: {
            rich: {
              a: {
                color: '#7bf5fe',
                align: 'center',
                fontSize: 23,
                fontFamily: 'SourceHanSansCN-Medium'
              },
              b: {
                align: 'center',
                fontSize: 64,
                fontFamily: 'SourceHanSansCN-Medium',
                color: '#fcb730'
              }
            },
            formatter: function (params) {
              if (params.data.customFields) {
                return '{a|接入摄像头数}' + '\n\n{b|' + params.data.customFields + '}'
              }
              return '{a|接入摄像头数}' + '\n\n{b|0}'
            },
            position: 'center',
            show: true,
            textStyle: {
              fontSize: '14',
              fontWeight: 'normal',
              color: '#fff'
            }
          },
          emphasis: {
            show: true,
            textStyle: {
              fontSize: '14',
              fontWeight: 'normal'
            }
          }
        },

        data: [{
          value: 75,
          itemStyle: {
            normal: {
              color: 'rgba(20,198,249,1)'
            }
          }
        },
        {
          value: 25,
          itemStyle: {
            normal: {
              color: 'rgba(0,0,0,0)'
            }
          }

        }]
      }

    ]
  }
export default {
  name: 'OnlineProbability',
  props: {
    cameraInfo: Object
  },
  data () {
    return {
      option: {}
    }
  },
  computed: {
    getOnlineRatio () {
      return this.cameraInfo.onlineRatio
    }
  },
  mounted () {
    this.initChart()
  },

  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option)
    }
  },
  watch: {
    getOnlineRatio (newV, oldV) {
      const { online, total } = this.cameraInfo
      if (online && total) {
        const lightV = parseInt((online / total * 0.75) * 100)
        option.series[0].data[0].value = lightV >= 75 ? 75 : parseInt(lightV)
        option.series[0].data[0]['customFields'] = this.cameraInfo.total
        this.chart.setOption(option)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.main{
  width: 346px;
  height: 343px;
  margin-top:29px;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  .chart-box{
    flex: 1;
    width: 100%;
  }
  .online-number{
    height: 51px;
    font-family: SourceHanSansCN-Regular;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 51px;
    letter-spacing: 0px;
    color: #e5fefe;
    .unit{
      font-family: Helvetica-Bold;
      font-size: 40px;
      font-weight: normal;
      letter-spacing: 0px;
      color: #e5fefe;
    }
  }
}

</style>
