export default {
  name: 'cook',
  props: {
    companyRatio: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option)

      // this.leftChart = echarts.init(this.$refs.leftChartRef)
      // this.leftChart.setOption(leftChartOption)
    },
    setChartData () {
      const { companyTotal, companyRatio } = this.companyRatio
      if (companyTotal) {
        let newData = []
        companyTotal.map(item => {
          if (item.controlUnitName === '学校食堂') {
            newData.push({
              value: item.count,
              name: '学校食堂',
              itemStyle: {
                normal: {
                  color: '#18e4c5'
                }
              }
            })
          } else if (item.controlUnitName === '社会餐饮') {
            newData.push({
              value: item.count,
              name: '社会餐饮',
              itemStyle: {
                normal: {
                  color: '#3278e1'
                }
              }
            })
          } else if (item.controlUnitName === '职工食堂') {
            newData.push({
              value: item.count,
              name: '职工食堂',
              itemStyle: {
                normal: {
                  color: '#dc739d'
                }
              }
            })
          } else if (item.controlUnitName === '农村聚餐') {
            newData.push({
              value: item.count,
              name: '农村聚餐',
              itemStyle: {
                normal: {
                  color: '#0dd5ed'
                }
              }
            })
          } else if (item.controlUnitName === '阳光车间') {
            newData.push({
              value: item.count,
              name: '阳光车间',
              itemStyle: {
                normal: {
                  color: '#f3a956 '
                }
              }
            })
          }
        })
        option.series[0].data = newData
      }
      if (companyRatio) {
        const xxst = companyRatio['学校食堂'] || 0
        const shcy = companyRatio['社会餐饮'] || 0
        const ncst = companyRatio['农村聚餐'] || 0
        const ygcj = companyRatio['阳光车间'] || 0
        const zgst = companyRatio['职工食堂'] || 0
        option.series.map(item => {
          if (item.name === '1') {
            item.data = this.getData1(xxst, '#18e4c5')
          } else if (item.name === '1-1') {
            item.data = this.getData2(xxst)
          } else if (item.name === '2') {
            item.data = this.getData1(shcy, '#3278e1')
          } else if (item.name === '2-2') {
            item.data = this.getData2(shcy)
          } else if (item.name === '3') {
            item.data = this.getData1(ncst, '#0dd5ed')
          } else if (item.name === '3-1') {
            item.data = this.getData2(ncst)
          } else if (item.name === '4') {
            item.data = this.getData1(ygcj, '#f3a956 ')
          } else if (item.name === '4-1') {
            item.data = this.getData2(ygcj)
          } else if (item.name === '5') {
            item.data = this.getData1(zgst, '#dc739d')
          } else if (item.name === '5-1') {
            item.data = this.getData2(zgst)
          }
        })
      }
      this.chart.setOption(option)
    },
    getData1 (value, color) {
      const v = parseInt(value)
      const lightV = v * 0.75

      // console.log(v, lightV ? 75 : parseInt(lightV))
      const data = [{
        value: lightV >= 75 ? 75 : parseInt(lightV),
        itemStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: color
              },
              {
                offset: 1,
                color: color
              }
            ])
          }
        },
        label: {
          normal: {
            formatter: function () {
              return v + '%'
            },
            position: 'center',
            show: true,
            textStyle: {
              fontSize: '28',
              fontWeight: 'normal',
              color: '#fff'
            }
          }
        }
      },
      {
        value: 25,
        itemStyle: placeHolderStyle
      }
      ]
      return data
    },
    getData2 (value) {
      // const v = parseInt(value)
      const data = [
        {
          value: 75,
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
