                  color: '#01babc'
                },
                {
                  offset: 1,
                  color: '#99da69'
                }
              ])
            }
          }
        },
        {
          value: 25,
          itemStyle: placeHolderStyle
        }
      ]
      return data
    }

  },
  watch: {
    companyRatio: {
      handler (newValue, oldValue) {
        console.log('data changed....')
        this.setChartData()
      },
      deep: true
    }
  }
